<template>
  <div class="invite-code">
    <div class="banner" :style="{backgroundImage: 'url(' + $assetsUrl + 'invite-code/banner.png)'}">
    </div>
    <div class="introduce">
      <div class="card">
        <div class="card-top">
          <div class="descrption">
            新用戶註冊後馬上送一個月15G免費流<br />
            量套餐,使用邀請碼更能再送半個月!
          </div>
          <div class="code-wrap">
            <span>你獲得的邀請碼</span>
            <p>{{code}}</p>
            <div class="button" @click="copyCode()">
              複製
            </div>
          </div>
        </div>
        <div class="line"></div>
        <div class="card-bottom">
          <div class="button" @click="toDownload()">下載APP領取</div>
          <p>請在註冊頁填寫您的邀請碼</p>
        </div>
      </div>
    </div>
    <div class="isWechat" v-if="isWechat" @click="isWechat = false">
      <div class="content" :style="{backgroundImage: 'url(' + $assetsUrl + 'invite-code/wechat.png)'}"></div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  name: 'invite-code',
  data() {
    return {
      code: '',
      isWechat: false,
      token: '',
    };
  },
  created() {
    this.code = this.$route.params.code;
  },
  methods: {
    copyCode() {
      const input = document.createElement('input');
      input.setAttribute('readonly', 'readonly');
      input.setAttribute('value', this.code);
      document.body.appendChild(input);
      input.setSelectionRange(0, 9999);
      input.select();
      if (document.execCommand('copy')) {
        document.execCommand('copy');
        Toast({
          message: '複製成功',
          duration: 1000,
        });
      }
      document.body.removeChild(input);
    },
    toDownload() {
      const ua = navigator.userAgent.toLowerCase();
      this.isWechat = ua.indexOf('micromessenger') !== -1;
      if (!this.isWechat) {
        this.$router.push('/');
      }
    },
  },
};
</script>

